import {ANIMATION_LIST_QUERY, ANIMATION_QUERY} from "../queries/queries";
import {createApolloFetch, FetchResult} from "apollo-fetch";
import {API} from "../constants/constants";

export type Animation = {
    Page: Page;
}

export type Page = {
    pageInfo: PageInfo;
    media: Array<Media>;
}

export type PageInfo = {
    total: number;
    perPage: number;
    currentPage: number;
    lastPage: number;
    hasNextPage: boolean;
}

export type Media = {
    id: number;
    title: MediaTitle;
    coverImage: MediaCoverImage;
    genres: Array<string>;
    status: string;
    description: string;
    startDate: FuzzyDate;
    averageScore: number;
    trailer: MediaTrailer;
    relations: MediaConnection;
    characters: CharacterConnection;
    siteUrl: string;
    bannerImage: string;
}

export type CharacterConnection = {
    nodes: Array<Character>;
}

export type Character = {
    id: number;
    name: CharacterName;
    image: CharacterImage;
    description: string;
}

export type CharacterName = {
    full: string;
}

export type CharacterImage = {
    large: string;
}

export type MediaConnection = {
    nodes: Array<Media>;
}

export type MediaTrailer = {
    id: string;
    site: string;
    thumbnail: string;
}

export type FuzzyDate = {
    year: string;
    month: string;
    day: string;
}

export type MediaTitle = {
    romaji: string;
    english: string;
    native: string;
}

export type MediaCoverImage = {
    extraLarge: string;
    large: string;
    medium: string;
}

export type AnimationsParam = {
    page: number;
    perPage: number;
    isAdult?: boolean;
    sort?: string;
    search?: string;
    genres?: Array<string>;
}

class AnimationService {
    private fetch = createApolloFetch({
        uri: API.ANILIST_CO
    });

    public async getAnimations(param: AnimationsParam): Promise<FetchResult> {
        return await this.fetch({
            query: ANIMATION_LIST_QUERY,
            variables: param
        });
    }

    public async getAnimation(id: number): Promise<FetchResult> {
        return await this.fetch({
            query: ANIMATION_QUERY,
            variables: {id: id}
        });
    }
}

export default AnimationService;
