import {action, decorate, observable} from "mobx";
import AnimationService, {AnimationsParam, Media, PageInfo} from "../../services/AnimationService";

class AnimationListStore {
    isLoading: boolean = false;
    param: AnimationsParam = {page: 1, perPage: 10};
    animations: Array<Media> = [];
    pageInfo: PageInfo = {
        currentPage: 1,
        hasNextPage: false,
        lastPage: 1,
        perPage: 10,
        total: 0
    };

    constructor(private animationService: AnimationService) {
    }

    getAnimations() {
        this.isLoading = true;
        const requestParam = {
            page: this.param.page,
            perPage: this.param.perPage,
            isAdult: !!this.param.isAdult,
            sort: !this.param.sort ? 'TRENDING_DESC' : this.param.sort
        };

        if (this.param.search) Object.assign(requestParam, {search: this.param.search});
        if (this.param.genres && this.param.genres.length > 0) Object.assign(requestParam, {genres: this.param.genres});
        this.param = requestParam;

        this.animationService.getAnimations(requestParam).then(response => {
            this.animations = response.data.Page.media;
            this.pageInfo = response.data.Page.pageInfo;
        }).finally(() => {
            this.isLoading = false;
        });
    }

    setParam(param: AnimationsParam) {
        this.param = param;
    }
}

decorate(AnimationListStore, {
    animations: observable,
    pageInfo: observable,
    param: observable,
    isLoading: observable,
    setParam: action,
    getAnimations: action,
});

export default AnimationListStore;
