import React from "react";
import {Card} from "antd";
import {Link} from "react-router-dom";
import {PAGE_PATHS} from "../../constants/constants";
import "./SimpleCard.scss";

const SimpleCard = ({id, title, coverImage, genres}: any) => {
    const {Meta} = Card;

    return <Link to={`${PAGE_PATHS.ANIMATION_LIST}/${id}`}>
        <div className={"simple-card"}>
        <Card hoverable cover={<img className={"simple-card-preview"} alt={title} src={coverImage}/>}>
            <Meta title={title} description={genres.join(' / ').substr(0, 25)}/>
        </Card>
        </div>
    </Link>
};

export default SimpleCard;
