import React, {Component} from 'react';
import AnimationService, {Media} from "../../services/AnimationService";
import {Button} from "antd";
import {FullScreenSpin} from "../../components/spin";
import "./AnimationDetail.scss";

type States = {
    media: Media | null,
}

class AnimationDetail extends Component<any, States> {
    private readonly animationService: AnimationService;

    constructor(props: any, context: States) {
        super(props, context);
        this.animationService = new AnimationService();
        this.state = {
            media: null,
        };
    }

    componentDidMount(): void {
        const {match} = this.props;
        const id = match.params.id;

        this.animationService.getAnimation(id).then(response => {
            console.log(response);
            this.setState({
                media: response.data.Media,
            });
        });
    }

    render() {
        if (this.state.media == null) {
            return <FullScreenSpin/>
        }

        const {media} = this.state;
        const characters = media.characters.nodes.map(character => {
            return (<div key={character.id} className={"character"}>
                <div className={"preview"}><img src={character.image.large}/></div>
                <div className={"info"}>
                    <h4>{character.name.full}</h4>
                    <p dangerouslySetInnerHTML={{__html: character.description}}/>
                </div>
            </div>)
        });

        let trailerUrl = '';
        if (media.trailer) {
            const trailerId = media.trailer.id;
            const trailerSite = media.trailer.site;

            if (trailerSite === 'youtube') {
                trailerUrl = `https://www.youtube.com/watch?v=${trailerId}`;
            } else if (trailerSite === 'dailymotion') {
                trailerUrl = `http://www.dailymotion.com/embed/video/${trailerId}`;
            }
        }
        return (
            <div className={"item-detail"}>
                <div className={"item-head"} style={{backgroundImage: `url(${media.bannerImage})`, backgroundSize: 'cover'}}>
                    <div className={"item-head-content"}>
                        <div className={"item-preview"}>
                            <img src={media.coverImage.large}/>
                        </div>
                        <div className={"item-info"}>
                            <div className={"info"}>
                                <h1>{media.title.romaji}</h1>
                                <h3>{media.genres.join(", ")}</h3>
                                <h3>Average Score {media.averageScore}</h3>
                            </div>
                            <div className={"trailer"}>
                                {trailerUrl ?
                                    <Button target={"_black"} href={trailerUrl}>Trailer</Button> : ''}
                                <Button target={"_black"} href={media.siteUrl}>More information</Button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={"item-content"}>
                    <div className={"main"}>
                        <div className={"section"}>
                            <h4>Characters</h4>
                            <div className={"characters"}>
                                {characters}
                            </div>
                        </div>
                    </div>
                    <div className={"side"}>
                        <div className={"section"}>
                            <h4>Story</h4>
                            <p dangerouslySetInnerHTML={{__html: media.description}}/>
                        </div>
                        <div className={"section"}>
                            <h4>Recommendations</h4>
                            <div className={"recommendations"}>
                                Preparing for service...
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default AnimationDetail;
