import React, {Component} from "react";
import {Input} from 'antd';
import {Logo} from "../logo";
import './DefaultLayout.scss';
import {Redirect, Route, RouteComponentProps, Switch} from "react-router-dom";
import {AnimationRouter} from "../../pages/animations";
import {PAGE_PATHS} from "../../constants/constants";

const { Search } = Input;

class DefaultLayout extends Component<RouteComponentProps> {
    onSearch = (value: string) => {
        this.props.history.push(`${PAGE_PATHS.ANIMATION_LIST}?search=${value}`);
    };

    render() {
        return (
            <div className="default-layout">
                <header className="default-header">
                    <Logo className="logo" />
                    <Search
                        className="search"
                        placeholder="input search text"
                        onSearch={this.onSearch}
                    />
                </header>
                <div className="default-content-layout">
                    <Switch>
                        <Route path={PAGE_PATHS.ANIMATION_LIST} component={AnimationRouter}/>
                        <Redirect from={'/'} to={PAGE_PATHS.ANIMATION_LIST} />
                    </Switch>
                </div>
            </div>
        );
    }
}

export default DefaultLayout;
