export const ANIMATION_LIST_QUERY = `
    query ($page: Int, $perPage: Int, $isAdult: Boolean, $sort: MediaSort, $search: String, $genres: [String]){
        Page(page:$page, perPage:$perPage) {
            pageInfo{
                total
                perPage
                currentPage
                lastPage
                hasNextPage
            },
            media(isAdult: $isAdult, sort: [$sort], search: $search, genre_in: $genres) {
                id
                title{
                    romaji
                    english
                    native
                },
                coverImage{
                    extraLarge
                    large
                    medium
                },
                genres
            }
        }
    }
`;

export const ANIMATION_QUERY = `
query ($id: Int) {
    Media (id: $id) {
        id
        title{
            romaji
            english
            native
        }
        coverImage{
            extraLarge
            large
            medium
        }
        genres
        status
        description
        startDate{
            year
            month
            day
        }
        averageScore
        trailer {
            site
            thumbnail
            id
        }
        relations {
            nodes {
                title {
                  romaji
                  english
                  native
                }
                description
                coverImage {
                  extraLarge
                  large
                  medium
                }
            }
        }
        characters {
            nodes {
                id
                name {
                    full
                }
                image {
                    large
                }
                description
            }
        }
        siteUrl
        bannerImage
    }
}
`;
