import React, {Component} from "react";
import {STORES} from "../../constants/constants";
import AnimationListStore from "../../stores/animation/AnimationListStore";
import {inject, observer} from "mobx-react";
import {SimpleCard} from "../../components/card";
import {Button, Checkbox, Icon, Pagination, Select} from "antd";
import {FullScreenSpin} from "../../components/spin";
import "./AnimationListV2.scss";
import {CheckboxChangeEvent} from "antd/es/checkbox";
import {RouteComponentProps} from "react-router-dom";
import queryString from "query-string";

interface Props extends RouteComponentProps {
    [STORES.ANIMATION_STORE]: AnimationListStore
}

class AnimationListV2 extends Component<Props> {
    componentDidMount(): void {
        this.reset();
    }

    componentDidUpdate(prevProps: Props) {
        if (this.props.location !== prevProps.location) {
            this.reset();
        }
    }

    onGenreChange = (e: CheckboxChangeEvent) => {
        let {genres} = this.props[STORES.ANIMATION_STORE].param;
        if (!genres) {
            genres = [];
        }

        if (e.target.checked) {
            genres.push(e.target.value);
        } else {
            genres = genres.filter(genre => genre !== e.target.value);
        }

        this.props[STORES.ANIMATION_STORE].setParam({
            ...this.props[STORES.ANIMATION_STORE].param,
            page: 1,
            genres: genres
        });

        this.getList();
    };

    onPageChange = (current: number, perPage: number = this.props[STORES.ANIMATION_STORE].param.perPage) => {
        this.props[STORES.ANIMATION_STORE].setParam({
            ...this.props[STORES.ANIMATION_STORE].param,
            page: current,
            perPage: perPage
        });
        this.getList();
    };

    onSortChange = (sort: string) => {
        this.props[STORES.ANIMATION_STORE].setParam({
            ...this.props[STORES.ANIMATION_STORE].param,
            sort: sort
        });
        this.getList();
    };

    getList = () => {
        this.props[STORES.ANIMATION_STORE].getAnimations();
    };

    reset = () => {
        const query = queryString.parse(this.props.location.search);
        this.props[STORES.ANIMATION_STORE].setParam({
            page: 1,
            perPage: 10,
            search: query.search as string || ''
        });

        this.getList();
    };

    render() {
        const {pageInfo, animations, isLoading, param} = this.props[STORES.ANIMATION_STORE];
        if (isLoading) {
            return <FullScreenSpin/>
        }

        const render = animations.map(media => (
            <SimpleCard key={media.id}
                        id={media.id}
                        title={media.title.romaji}
                        coverImage={media.coverImage.large}
                        genres={media.genres}
            />
        ));

        const genres = !param.genres ? [] : param.genres;
        return <div className="content-wrap">
            <aside className="sidebar">
                <div className="main-title">
                    <div>Filter</div>
                    <Button size={"small"} onClick={this.reset}>Reset<Icon type="redo"/></Button>
                </div>
                <div className="sidebar-filter border-bottom">
                    <div className="sidebar-filter-title">Genres</div>
                    <div className="sidebar-filter-item">
                        <div><Checkbox onChange={this.onGenreChange} value={"Drama"} checked={genres.some(genre => genre === 'Drama')}>Drama</Checkbox></div>
                        <div><Checkbox onChange={this.onGenreChange} value={"Sci-Fi"} checked={genres.some(genre => genre === 'Sci-Fi')}>Sci-Fi</Checkbox></div>
                        <div><Checkbox onChange={this.onGenreChange} value={"Action"} checked={genres.some(genre => genre === 'Action')}>Action</Checkbox></div>
                        <div><Checkbox onChange={this.onGenreChange} value={"Comedy"} checked={genres.some(genre => genre === 'Comedy')}>Comedy</Checkbox></div>
                        <div><Checkbox onChange={this.onGenreChange} value={"Romance"} checked={genres.some(genre => genre === 'Romance')}>Romance</Checkbox></div>
                        <div><Checkbox onChange={this.onGenreChange} value={"Fantasy"} checked={genres.some(genre => genre === 'Fantasy')}>Fantasy</Checkbox></div>
                    </div>
                </div>
            </aside>
            <div className="content">
                <div style={{padding: 10, fontSize: '1.5em', marginBottom: 10}}>Total of {pageInfo.total}</div>
                <div className="pagination-wrap" style={{marginBottom: 10}}>
                    <div className="sort">
                        <Select defaultValue={param.sort} onChange={this.onSortChange}
                                style={{width: 160}}>
                            <Select.Option value="TRENDING_DESC">Trending</Select.Option>
                            <Select.Option value="FAVOURITES_DESC">Favourites</Select.Option>
                            <Select.Option value="SCORE_DESC">Score</Select.Option>
                            <Select.Option value="START_DATE_DESC">Start date</Select.Option>
                        </Select>
                    </div>
                    <div className="pagination">
                        <Pagination
                            showSizeChanger
                            defaultCurrent={pageInfo.currentPage}
                            defaultPageSize={pageInfo.perPage}
                            onShowSizeChange={this.onPageChange}
                            onChange={this.onPageChange}
                            total={pageInfo.total}
                        />
                    </div>
                </div>
                <div>
                    {render}
                </div>
                <div className="pagination-wrap">
                    <div className="sort">
                        <Select defaultValue={param.sort} onChange={this.onSortChange}
                                style={{width: 160}}>
                            <Select.Option value="TRENDING_DESC">Trending</Select.Option>
                            <Select.Option value="FAVOURITES_DESC">Favourites</Select.Option>
                            <Select.Option value="SCORE_DESC">Score</Select.Option>
                            <Select.Option value="START_DATE_DESC">Start date</Select.Option>
                        </Select>
                    </div>
                    <div className="pagination">
                        <Pagination
                            showSizeChanger
                            defaultCurrent={pageInfo.currentPage}
                            defaultPageSize={pageInfo.perPage}
                            onShowSizeChange={this.onPageChange}
                            onChange={this.onPageChange}
                            total={pageInfo.total}
                        />
                    </div>
                </div>
            </div>
        </div>;
    }
}

export default inject(STORES.ANIMATION_STORE)(observer(AnimationListV2));
