import React from 'react';
import {Route, Switch} from "react-router-dom";
import {AnimationDetail, AnimationListV2} from "./index";

const AnimationRouter = ({match}: any) => {
    return (
        <Switch>
            <Route path={`${match.path}/:id`} component={AnimationDetail}/>
            <Route path={`${match.path}`} component={AnimationListV2}/>
        </Switch>
    );
};

export default AnimationRouter;
